                        width="60%" style="height: auto; overflow: hidden;">
                        
                        <div style="height: 380px; overflow: auto;">

                        <el-tag style="font-size:16px;" type="danger"></el-tag>
                        <el-checkbox-group v-model="select" fill="#409EFF" text-color="#FFFFFF" style="padding-top: 10px;">
                        <el-checkbox-button style="margin: 4px ; border: 1px solid lightgrey; border-radius: 4px;" v-for="city in tag1" :label="city" :key="city">{{city}}</el-checkbox-button>
                        </el-checkbox-group>
                        
                        <el-tag style="font-size:16px;" ></el-tag>
                        <el-checkbox-group v-model="select" fill="#409EFF" text-color="#FFFFFF" style="padding-top: 10px;">
                        <el-checkbox-button style="margin: 4px ; border: 1px solid lightgrey; border-radius: 4px;" v-for="city in tag0" :label="city" :key="city">{{city}}</el-checkbox-button>
                        </el-checkbox-group>
                    
                        <el-tag style="font-size:16px;" type="success"></el-tag>
                        <el-checkbox-group v-model="select" fill="#67C23A" text-color="#FFFFFF" style="padding-top: 10px;">
                        <el-checkbox-button style="margin: 4px ; border: 1px solid lightgrey; border-radius: 4px;" v-for="city in tag2" :label="city" :key="city">{{city}}</el-checkbox-button>
                        </el-checkbox-group>
                        
                    
                        <el-tag style="font-size:16px;" type="info"></el-tag>
                        <el-checkbox-group v-model="select" fill="#909399" text-color="#FFFFFF" style="padding-top: 10px;">
                        <el-checkbox-button style="margin: 4px ; border: 1px solid lightgrey; border-radius: 4px;" v-for="city in tag3" :label="city" :key="city">{{city}}</el-checkbox-button>
                        </el-checkbox-group>
                        
                    
                        <el-tag style="font-size:16px;" type="warning"></el-tag>
                        <el-checkbox-group v-model="select" fill="#E6A23C" text-color="#FFFFFF" style="padding-top: 10px;">
                        <el-checkbox-button style="margin: 4px ; border: 1px solid lightgrey; border-radius: 4px;" v-for="city in tag4" :label="city" :key="city">{{city}}</el-checkbox-button>
                        </el-checkbox-group>
                        
                    
                        <el-tag style="font-size:16px;" type="danger"></el-tag>
                        <el-checkbox-group v-model="select" fill="#F56C6C" text-color="#FFFFFF" style="padding-top: 10px;">
                        <el-checkbox-button style="margin: 4px ; border: 1px solid lightgrey; border-radius: 4px;" v-for="city in tag5" :label="city" :key="city">{{city}}</el-checkbox-button>
                        </el-checkbox-group>

                        <el-tag style="font-size:16px;"></el-tag>
                        <el-checkbox-group v-model="select" fill="#F56C6C" text-color="#FFFFFF" style="padding-top: 10px;">
                        <el-checkbox-button style="margin: 4px ; border: 1px solid lightgrey; border-radius: 4px;" v-for="city in tag6" :label="city" :key="city">{{city}}</el-checkbox-button>
                        </el-checkbox-group>

                        <el-tag style="font-size:16px;" type="success"></el-tag>
                        <el-checkbox-group v-model="select" fill="#F56C6C" text-color="#FFFFFF" style="padding-top: 10px;">
                        <el-checkbox-button style="margin: 4px ; border: 1px solid lightgrey; border-radius: 4px;" v-for="city in tag7" :label="city" :key="city">{{city}}</el-checkbox-button>
                        </el-checkbox-group>
                        </div>

                        

                        <span slot="footer" class="dialog-footer">
                            <el-button type="primary" @click="ClickMeToSelect"> </el-button>
                        </span>
                        </el-dialog>
                    </div>
                    <!--  -->
                    <div style="width:180px; font-size:16px; margin-left:10px; box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1)">
                        <el-select clearable v-model="value" placeholder="">
                            <el-option
                            v-for="item in Difficulity"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                            </el-option>
                        </el-select>
                    </div>
                    <!--  -->
                    <div style="font-size:16px; margin-left:30px; display: flex; justify-content: center; align-content: center;" >
                        <el-input
                        clearable
                        placeholder=" / "
                        prefix-icon="el-icon-search"
                        v-model="input"
                        @keyup.native.enter="ClickMeToSearch">
                        </el-input>
                    </div>
                </div>
            </div>
            <!--  -->
            <div style="padding-top: 10px; clear: both; min-height: 20px; height: auto; font-size: 14px;">
                <div style="font-size: 16px; font-weight: 600;"></div>
                <div style="padding: 10px; font-size: 14px;">
                    <el-tag
                        v-for="(item,index) in select"
                        :key="index"
                        type='info'
                        effect="dark"
                         style="margin: 5px;">
                        {{ item }}
                        <!-- <el-button icon= size="mini" style="height: 10px;"></el-button> -->
                        <i class="el-icon-close" @click="ClickMeBySelect(item)"></i>
                    </el-tag>
                </div>
            </div>
            <!--  -->
            <div class="head-title-in-3">
                <div style="height: auto; width: 100%; display: flex; float: left;">
                    <div style="width: 80%; font-size:16px; color: rgba(0,0,0,.45);">{{ this.$store.state.problem.question.length }}</div>
                    <div style="width: 20%; font-size:16px; color: #3C9CDC;" @click="ClickMeToClear"></div>
                </div>
            </div>
        </div>

        <div class="body">
            <table class="table table-striped table-hover" v-loading="loading">
                <thead>
                    <tr style="font-size: 14px;">
                        <th scope="col-2"></th>
                        <th scope="col-2"></th>
                        <th scope="col-6"> 
                            <el-switch
                            v-model="IsVis"
                            active-text=""
                            inactive-text="">
                            </el-switch>
                        </th>
                        <th scope="col-2"></th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="t in showpage" :key="t.pbid">
                        <th class="col-2" style="font-size: 13px;" @click="ClickMeGoproblem(t)">{{ t.pbid }}</th>
                        <td class="col-2" style="font-size: 13px;" @click="ClickMeGoproblem(t)">{{ t.name }}</td>
                        <td class="col-6" style="font-size: 13px;">
                            <span v-show="IsVis">
                                <span v-show="t.Basic !== ''">
                                    {{ t.altag }}
                                </span>
                                <span v-show="t.Basic === ''">
                                    
                                </span>
                            </span>
                        </td>
                        <td class="col-2" style="font-size: 13px;">{{ t.difficulty }}</td>
                    </tr>
                </tbody>
            </table>

            <!--  -->
            <div class="block">
                <span class="demonstration"></span> 
                <!-- :page-size="100" 1001000  ==> k  :page-size="(1000 + k - 1) / k"-->
                <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page.sync="currentPage3"
                :page-size="pageSize" 
                layout="prev, pager, next, jumper"
                :total="1000">
                </el-pagination>
            </div>
        </div>
    </div>
</template>

<script>
    const tag0 = ['', '', '', '', '', '', '', '', '']
    const tag1 = ['', '', '', '', '', '()', '', ''];
    const tag2 = ['', '', '', 'AC', 'KMP', '', ''];
    const tag3 = ['DP', '', 'DP', 'DP', 'DP', 'DP'];
    const tag4 = ['', 'DFS', 'BFS', '', '', '', '', ''];
    const tag5 = ['', '', '', 'GCD', '', ''];
    const tag6 = ['', 'Kruskal', '', '', "Dijkstra", '', 'spfa', 'bellman-ford', '', '', '',
                '', 'Tarjin', '', '', 'Lca'];
    const tag7 = ['', '', '', ''];
    export default {
        name:'question',
        components: {
            
        },
        data() {
            return {
                pbfrom: '1', // 
                select: [],
                tag0: tag0,
                tag1: tag1,
                tag2: tag2,
                tag3: tag3,
                tag4: tag4,
                tag5: tag5,
                tag6: tag6,
                tag7: tag7,
                dialogVisible: false, //
                loading: true,
                currentPage3: 1, // 
                value:'', /*  */
                Difficulity:[
                    {label:'', value:''},
                    {label:'', value:''},
                    {label:'', value:''}
                ],
                options: [
                {
                    label: '',
                    options: [{
                        value: '',
                        label: ''
                    }, {
                        value: '',
                        label: ''
                    }, {
                        value: '',
                        label: ''
                    }, {
                        value: '',
                        label: ''
                    }, {
                        value: '',
                        label: ''
                    }]
                }, 
                {
                    label: '',
                    options: [{
                        value: '',
                        label: ''
                    }, {
                        value: '',
                        label: ''
                    }, {
                        value: '',
                        label: ''
                    }, {
                        value: '',
                        label: ''
                    }, {
                        value: '',
                        label: ''
                    }, {
                        value: 'DP',
                        label: 'DP'
                    }]
                },
                {
                    label: '',
                    options: [{
                        value: 'Chengdu',
                        label: 'gcd'
                    }, {
                        value: 'Shenzhen',
                        label: ''
                    }, {
                        value: 'Guangzhou',
                        label: ''
                    }, {
                        value: 'Dalian',
                        label: ''
                    }]
                }
                ],
                IsVis:true,
                input: '', // 
                showpage:[],
                Topic: [],

                pageSize: 1
            }
        },
        watch: {
            select: {
                deep:true,
                handler(value) {
                    console.log("new select == >", value)
                    this.$store.dispatch("problem/SelectQuestionList", {
                        list: value,
                        option: this.value,
                        input: this.input,
                        pbfrom: this.pbfrom,
                    })

                    // 
                    this.loading = true
                    this.currentPage3 = 1
                    setTimeout(() => {
                        const Topic = this.$store.state.problem.question // 
                        this.showpage = Topic.slice(0, Math.min(30, Topic.length)) // 30
                        this.loading = false;
                    }, 500)

                    // if (this.$store.state.problem.question.length !== 0) {
                    //     const Topic = this.$store.state.problem.question // 
                    //     this.showpage = Topic.slice(0, Math.min(30, Topic.length)) // 30
                    //     this.loading = false
                    // }
                    // else {
                    //     const t = setInterval(() => {
                    //       if (this.$store.state.problem.question.length !== 0) {
                    //           console.log("hh")
                    //           const Topic = this.$store.state.problem.question // 
                    //           this.showpage = Topic.slice(0, Math.min(30, Topic.length)) // 30
                    //           this.loading = false
                    //           clearInterval(t); // 
                    //           console.log("")
                    //       }
                    //     }, 100);
                    // }
                }
            },
            pbfrom: {
                deep:true,
                handler(value) {
                    console.log("new pbfrom == >", value)
                    this.$store.dispatch("problem/SelectQuestionList", {
                        list: this.select,
                        option: this.value,
                        input: this.input,
                        pbfrom: value,
                    })

                    // 
                    this.loading = true
                    this.currentPage3 = 1
                    setTimeout(() => {
                        const Topic = this.$store.state.problem.question // 
                        this.showpage = Topic.slice(0, Math.min(30, Topic.length)) // 30
                        this.loading = false;
                    }, 500)
                }
            },
            value: {
                deep:true,
                handler(value) {
                    console.log("new value == > ", value)
                    this.$store.dispatch("problem/SelectQuestionList", {
                        list: this.select,
                        option: value,
                        input: this.input,
                        pbfrom: this.pbfrom,
                    })

                    // 
                    this.loading = true
                    this.currentPage3 = 1
                    setTimeout(() => {
                        const Topic = this.$store.state.problem.question // 
                        this.showpage = Topic.slice(0, Math.min(30, Topic.length)) // 30
                        this.loading = false;
                    }, 500)
                }
            },
            input: { //    
                deep:true,
                handler(value) {
                    console.log("new input == > ", value)
                }
            }
        },
        mounted() {
            this.$store.dispatch("problem/GetQuestionList")

            if (this.$store.state.problem.question.length !== 0) {
                const Topic = this.$store.state.problem.question // 
                const s = (this.$store.state.problem.question.length) / 30 // 
                this.pageSize = 1000 / s;
                this.showpage = Topic.slice(0, Math.min(30, Topic.length)) // 30
                this.loading = false;
            }
            else {
                const t = setInterval(() => {
                    if (this.$store.state.problem.question.length !== 0) {
                        const Topic = this.$store.state.problem.question // 
                        const s = (this.$store.state.problem.question.length) / 30 // 
                        this.pageSize = 1000 / s;
                        this.showpage = Topic.slice(0, Math.min(30, Topic.length)) // 30
                        this.loading = false;
                        clearInterval(t); // 
                    }
                }, 100);
            }
        },
        methods: {
            ClickMeToSearch() {
                console.log("")
                this.$store.dispatch("problem/SelectQuestionList", {
                    list: this.select,
                    option: this.value,
                    input: this.input,
                    pbfrom: this.pbfrom,
                })

                // 
                this.loading = true
                setTimeout(() => {
                    const Topic = this.$store.state.problem.question // 
                    this.showpage = Topic.slice(0, Math.min(30, Topic.length)) // 30
                    this.loading = false;
                }, 500)
            },
            ClickMeToClear() { // 
                this.select = this.select.filter((value) => {
                    return value === ''
                })
                this.value = ''
                this.input = ''
            },
            ClickMeBySelect(item) { // selectitem
                this.select = this.select.filter((value) => {
                    return value !== item
                })
            },
            ClickMeToOpen() {
                this.dialogVisible = true
            },
            ClickMeToSelect() { // 
                this.dialogVisible = false
            },
            /*  */
            ClickMeGoproblem(t) {
                let routeData = this.$router.resolve({
                    path:`/Problem/Content/${t.pbid}`,
                });
                window.open(routeData.href, '_blank');
            },
            handleSizeChange(val) {
                console.log(` ${val} `);
            },
            handleCurrentChange(val) {
                console.log(`: ${val}`);
                const Topic = this.$store.state.problem.question
                this.loading = true // 
                setTimeout(() => { //  
                    this.showpage.length = 0
                    this.showpage = Topic.slice((val - 1) * 30, Math.min(val * 30, Topic.length))
                    this.loading = false // 
                }, 500);
                console.log(this.showpage.length)
            }
        }
    }
</script>

