<template>
    <div style="width: 100%; height:auto;">
        <div class="head">
            <!--  -->
            <div style="width:225px; height:66px;">
                <img style="cursor: pointer;" width="100%" height="100%" :src="content.image" alt="AL" @click="ClickMeGoContestPage">
            </div>      
            <!--  -->
            <div style="margin-left:20px; height:75%; width:48%;">
                <div style="font-size:14px; color:#333333; font-size:18px; cursor: pointer;"  @click="ClickMeGoContestPage">
                AL-{{content.title}}
                </div>
                <div style="font-size:14px; color:#8690A8; margin-top:4px">
                    <i class="el-icon-eleme"></i> 
                    :{{content.startTime}}  {{content.closeTime}}
                    <!-- {{}}{{}}{{}} {{}}:{{}}  {{}}{{}}{{}} {{}}:{{}} -->
                </div>
                <div style="font-size:14px; color:#8690A8; margin-top:4px">
                    <i class="el-icon-message-solid"></i> 
                    :{{content.time}}<!-- {{}}{{}}{{}} {{}}:{{}}  {{}}{{}}{{}} {{}}:{{}} -->
                </div>
                <div style="font-size:14px; color:#8690A8; margin-top:4px">
                    <i class="el-icon-s-custom"></i> 
                    :{{content.participant}}<!-- {{}} -->
                </div>
            </div>
            <!--  -->
            <div>
                <!--  -->
                <el-button style="margin-left:60px" type="info" round @click="ClickMeGoContestPage"></el-button>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name:'CanCompete',
        props:['content'],
        data() {
            return {
                obj:""
            }
        },
        methods: {
            ClickMeGoContestPage() {
                console.log("")
                let routeData = this.$router.resolve({
                    path:`/contestPage/competeProblem/${this.content.id}`,
                });
                window.open(routeData.href, '_blank');
            }
        },
    }
</script>

<style scoped>
.head {
    margin-top:10px;
    background-color:white;
    width: 100%;
    height: 150px;
    /* margin-left: 2%; */
    display: flex;
    float: left;
    align-items: center;
    border: 1px solid lightgrey;
    border-left: 5px solid #AAB4BF;
}
</style>