import $ from 'jquery'

export default {
    state: {
        id: "",
        username: "",
        name:'',
        rating:'',
        photo: "",
        token: "",
        about:"",
        rank:"",
        is_login: false,
        pulling_info: true, // 
    },
    getters: {
    },
    mutations: {
        updateUser(state, user) { //
            console.log("user = ", user)
            state.id = user.id;
            state.username = user.username;
            state.photo = user.photo;
            state.is_login = user.is_login;
            state.name = user.name;
            state.rating = user.rating;
            state.about = user.about;
            state.rank = user.rank;
        },
        updateToken(state, token) { //Token
            state.token = token;
        },
        logout(state) { // 
            state.id = "";
            state.username = "";
            state.photo = "";
            state.token = "";
            state.name = "";
            state.rating = "";
            state.about = "";
            state.rank = "",
            state.is_login = false;
        },
        updatePullingInfo(state, pulling_info) {
            state.pulling_info = pulling_info
        },
    },
    actions: {
        login(context, data) { // 
            $.ajax({
                url: "https://app6132.acapp.acwing.com.cn/api/user/account/token/",
                type: "post",
                data: { //
                    username: data.username,
                    password: data.password,
                },
                success(resp) {
                    if (resp.error_message === "success") {
                        localStorage.setItem("jwt_token", resp.token) // token 
                        context.commit("updateToken", resp.token); // Token
                        data.success(resp);
                    } else {
                        console.log("")
                        console.log(resp)
                        data.error(resp);
                    }
                },
                error(resp) {
                    console.log("")
                    console.log(resp)
                    // data.error(resp);
                }
            });
        },
        getinfo(context, data) {
            $.ajax({
                url: "https://app6132.acapp.acwing.com.cn/api/user/account/info/",
                type: "get",
                headers: {
                    Authorization: "Bearer " + context.state.token,
                },
                success(resp) {
                    if (resp.error_message === "success") {
                        context.commit("updateUser", {
                            ...resp,
                            is_login: true,
                        });
                        data.success(resp);
                        // console.log(": ", resp)
                    } else {
                        console.log("")
                    }
                },
                error(resp) {
                    // data.error(resp);
                    console.log(" resp: ", resp.error_message)
                }
            })
        },
        logout(context) {
            localStorage.removeItem("jwt_token") // token 
            context.commit("logout");
        }
    },
}