<script>
    import $ from 'jquery'

    export default {
        name:'Info',
        data() {
            return {
                name:'',
                about:'',
                photo:'',
                dialogFormVisible: false,
                formLabelWidth: '120px',

                imageName:'',
                image:'',

                imageUrl: '',
                imageType: '',
                fileToUpload: null,
                extension:'',
            }
        },
        methods: {
            handleFileUpload(event) { // 
                const file = event.target.files[0];
                
                const reader = new FileReader();

                reader.onload = (e) => {
                    this.imageUrl = e.target.result; // URL
                    // console.log("url == >", this.imageUrl)
                    this.fileToUpload = file; // 
                    this.photo = file.name
                    // 
                    const extension = file.name.split('.').pop().toLowerCase();
                    // MIME
                    const fileType = file.type;
                    // console.log(file)
                    console.log("", extension)
                    this.extension = extension
                    this.image = "https://app6132.acapp.acwing.com.cn/static/image/" +  this.imageName + "." + extension
                    // console.log("type", fileType)
                    // 
                    this.imageType = fileType;
                };

                if (file === null || file === undefined) {
                    this.photo = ""
                    this.imageUrl = ""
                    this.imageType = ""
                    this.fileToUpload = null
                    console.log("")
                    return
                }
                // DataURL
                reader.readAsDataURL(file);
            },
            uploadFile() { //  url
                if (!this.fileToUpload) {
                    console.error('  ');
                    return;
                }
                const vue = this
                // FormData
                const formData = new FormData();
                formData.append('image', this.fileToUpload);
                formData.append('userid', this.$store.state.me.id)
                // $.ajaxURL/upload
                $.ajax({
                    url: 'https://app6132.acapp.acwing.com.cn/jdg/upload/head/',
                    method: 'POST',
                    data: formData,
                    processData: false,
                    contentType: false,
                    success: (response) => {
                        console.log('');
                        
                        const message = vue.$message

                        const name = vue.name.trim()
                        const about = vue.about.trim()
                        const photo = vue.photo.trim()

                        const store = vue.$store

                        $.ajax({
                            url:'https://app6132.acapp.acwing.com.cn/api/user/account/update/',
                            type:'post',
                            data: {
                                name:name,
                                about:about,
                                photo:'https://app6132.acapp.acwing.com.cn/static/head/' + store.state.me.id + "." + vue.extension,
                            },
                            headers: {
                                Authorization: "Bearer " + store.state.me.token
                            },
                            success(resp) {
                                if (resp.error_message === "success") {
                                    console.log("")
                                    //  getinfo updateuser
                                    store.dispatch("getinfo", {
                                        success() {
                                            store.commit("updatePullingInfo", false)
                                        },
                                        error() {
                                            store.commit("updatePullingInfo", false)
                                        },
                                    })
                                    message({
                                        type: 'success',
                                        message: "",
                                    })
                                } else {
                                    message({
                                        type: 'warning',
                                        message: resp.error_message,
                                    })
                                }
                            },
                        })
                        this.name = this.about = this.photo = ""
                    },
                    error: (error) => {
                        console.error(':', error);
                    }
                });
            },
            open() {
                this.dialogFormVisible = true
            },
            cancel() {
                this.dialogFormVisible = false
                this.$message({
                    type: 'info',
                    message: ''
                });      
                this.name = this.about = this.photo = ""
            },
            ul() {
                const message = this.$message

                const name = this.name.trim()
                const about = this.about.trim()
                const photo = this.photo.trim()

                const vue = this
                const store = this.$store

                $.ajax({
                    url:'https://app6132.acapp.acwing.com.cn/api/user/account/update/',
                    type:'post',
                    data: {
                        name:name,
                        about:about,
                        photo:photo,
                    },
                    headers: {
                        Authorization: "Bearer " + store.state.me.token
                    },
                    success(resp) {
                        if (resp.error_message === "success") {
                            console.log("")
                            //  getinfo updateuser
                            store.dispatch("getinfo", {
                                success() {
                                    store.commit("updatePullingInfo", false)
                                },
                                error() {
                                    store.commit("updatePullingInfo", false)
                                },
                            })
                            message({
                                type: 'success',
                                message: "",
                            })
                        } else {
                            message({
                                type: 'warning',
                                message: resp.error_message,
                            })
                        }
                    },
                })
                this.name = this.about = this.photo = ""
            },
            accept() {
                this.dialogFormVisible = false
                

                if (this.fileToUpload !== null) {
                    this.uploadFile()
                } else {
                    this.ul()
                }
            },
        }
    }
</script>

