<template>
    <div style="width: 100%; height:auto;">
        <div class="head">
            <!--  -->
            <div style="width:225px; height:66px;">
                <img style="cursor: pointer;" width="100%" height="100%" :src="content.image" alt="AL" @click="ClickMeGoContestPage">
            </div>      
            <!--  -->
            <div style="margin-left:20px; height:75%; width: 48%;">
                <div style="font-size:14px; color:#333333; font-size:18px; cursor: pointer;" @click="ClickMeGoContestPage">
                AL-{{content.title}}
                </div>
                <div style="font-size:14px; color:#8690A8; margin-top:4px">
                    <i class="el-icon-eleme"></i> 
                    :{{ content.startTime }}  {{content.closeTime}}
                    <!-- {{}}{{}}{{}} {{}}:{{}}  {{}}{{}}{{}} {{}}:{{}} -->
                </div>
                <div style="font-size:14px; color:#8690A8; margin-top:4px">
                    <i class="el-icon-message-solid"></i> 
                    :{{ content.time }}<!-- {{}}{{}}{{}} {{}}:{{}}  {{}}{{}}{{}} {{}}:{{}} -->
                </div>
                <div style="font-size:14px; color:#8690A8; margin-top:4px">
                    <i class="el-icon-s-custom"></i> 
                    :{{content.participant}}<!-- {{}} -->
                </div>
            </div>
            <!--  -->
            <div>
                <!--  -->
                <el-button v-show="!value" style="margin-left:60px" type="success" round @click="RegisterCompete"></el-button>
                <el-button v-show="value" style="margin-left:60px" type="danger" round @click="DeleteRegister"></el-button>
            </div>
        </div>
    </div>
</template>

<script>
    import { Loading } from 'element-ui';
    import Url from '@/constant/Url';
    import $ from 'jquery'
    export default {
        name:'CanCompete',
        props:['content'],
        data() {
            return {
                isShow: false,
                value: false,
                info:{},
                options:{}
            }
        },
        mounted() {
            if (this.$store.state.me.id !== "") this.ReadRegister()
            else {
                const t = setInterval(() => {
                    if (this.$store.state.me.id !== "") {
                        this.ReadRegister()
                        clearInterval(t); // 
                    }
                }, 100);
            }
        },
        methods: {
            ClickMeGoContestPage() {
                this.$message({
                    message: '',
                    type: 'warning'
                });

                // console.log("")
                // let routeData = this.$router.resolve({
                //     path:`/contestPage/competeProblem/${this.content.id}`,
                // });
                // window.open(routeData.href, '_blank');
            },
            DeleteRegister() {
                const store = this.$store
                const vue = this

                $.ajax({
                    url: Url.dRegisterCompete,
                    type:'post',
                    headers: {
                        Authorization: "Bearer " + store.state.me.token
                    },
                    data: {
                        id: vue.info.id
                    },
                    success(resp) {
                        if (resp.error_message === 'success') {
                            vue.ReadRegister()
                            // this.$message.error('');
                            vue.$message({
                                message: '',
                                type: 'error'
                            });
                        } 
                    }
                })
            },
            ReadRegister() {
                const store = this.$store
                const vue = this
                $.ajax({
                    url: Url.rRegisterCompete,
                    type:'post',
                    headers: {
                        Authorization: "Bearer " + store.state.me.token
                    },
                    data: {
                        userId: store.state.me.id,
                        competeId: vue.content.id
                    },
                    success(resp) {
                        if (resp.error_message === 'success') {
                            vue.info = JSON.parse(resp.value)
                            vue.isShow = true
                            vue.value = true // 
                        } else {
                            vue.isShow = true
                            vue.value = false // 
                        }
                    }
                })
            },
            RegisterCompete() {
                if (!this.$store.state.me.is_login) {
                    this.$notify({
                    title: '',
                    message: '',
                    });
                    return ;
                }
                const store = this.$store
                const vue = this

                $.ajax({
                    url: Url.cRegisterCompete,
                    type:'post',
                    headers: {
                        Authorization: "Bearer " + store.state.me.token
                    },
                    data: {
                        userName: store.state.me.name,
                        userId: store.state.me.id,
                        rating: store.state.me.rating,
                        competeId: vue.content.id
                    },
                    success(resp) {
                        if (resp.error_message === 'success') {
                            vue.ReadRegister()
                            vue.$message({
                                message: '',
                                type: 'success'
                            });
                        } 
                    }
                })
            }
        },
    }
</script>

<style scoped>
.head {
    margin-top:10px;
    background-color:white;
    width: 100%;
    height: 150px;
    /* margin-left: 2%; */
    display: flex;
    float: left;
    align-items: center;
    border: 1px solid lightgrey;
    border-left: 5px solid  #25BB9B;
}
</style>