<template>
    <div style="min-height:90h; max-height:90vh">
        <!-- <vue-markdown :source="content"></vue-markdown> -->
        <div class="container">
            <div class="row">
                <div class="col-1"></div>
                <div class="col-1" style="padding: 5px;">
                    <a href="/function">
                        <img src="../../../../assets//Logo//Logo2.png" alt="" width="120px">
                    </a>
                </div>
                <div class="col-8"></div>
                <div class="col">
                    <div style="max-height:10vh; min-height:10vh; padding:13px">
                    <el-button @click="goBack" type="success" plain></el-button>
                    <el-button @click="outputENter" type="success"></el-button>
                    </div>
                </div>
            </div>
        </div>
        <div style="min-height:6vh; max-height: 6vh;">
            <el-input
            maxlength="100"
            show-word-limit
            style="height: 10vh;"
            placeholder=""
            v-model="title"
            clearable>
            </el-input>
        </div>
        <div class="home">
            <mavon-editor :codeStyle="codeStyle" :toolbars="toolbars" style="min-height:84vh; max-height: 84vh;" :tabSize="tabSize" v-model="content" :ishljs="true" ref="md" @change="change"/>
        </div>
    </div>
</template>

<script>
    // import VueMarkdown from 'vue-markdown'
    import $ from 'jquery'
    export default {
        name:'MakeNotes',
        components: {
            // VueMarkdown,
        },
        // props:["allContent"],
        data() {
            return {
                title:'',
                content: "[//]: # (blablabla ^^)\n\n### \n\nblablabla\n\n#### \n\n```\nblablabla\n```\n\n\n----------\n\n### 1\n\n##### ()  $O(n^2)$\n\nblablabla\n\n#### \n\n#### \n\n#### C++ \n\n```\nblablabla\n```\n\n----------\n\n",
                tabSize:4,
                page_article:undefined,
                html_content:undefined,
                md_content:undefined,
                toolbars: {
                    bold: true, // 
                    italic: true, // 
                    header: true, // 
                    underline: true, // 
                    strikethrough: true, // 
                    mark: true, // 
                    superscript: true, // 
                    subscript: true, // 
                    quote: true, // 
                    ol: true, // 
                    ul: true, // 
                    link: true, // 
                    imagelink: true, // 
                    code: true, // code
                    table: true, // 
                    fullscreen: true, // 
                    readmodel: true, // 
                    htmlcode: true, // html
                    help: true, // 
                    undo: true, // 
                    redo: true, // 
                    trash: true, // 
                    save: true, // eventssave
                    navigation: true, // 
                    alignleft: true, // 
                    aligncenter: true, // 
                    alignright: true, // 
                    subfield: true, // 
                    preview: true // 
                },
                codeStyle:'monokai-sublime',//
                test_html:undefined,
            }
        },
        methods:{
            goBack() {
                this.$router.back(); // 
            },
            outputENter(){ //
                const vue = this
                const store = this.$store
                const route = this.$route
                const router = this.$router

                console.log(JSON.stringify(this.test_html))   //  
                
                $.ajax({
                    url: 'https://app6132.acapp.acwing.com.cn/api/notes/add/',
                    type: "post",
                    headers: {
                        Authorization: "Bearer " + store.state.me.token,
                    },
                    data: {
                        title: vue.title,
                        pbid: route.params.id,
                        markdown: vue.content,
                        content: vue.test_html,
                    },
                    success(resp) {
                        if (resp.error_message === 'success') {
                            console.log("id == > ", resp.id)
                            router.push({
                                name:'ShowNotes',
                                params: {
                                    id: resp.id
                                }
                            })
                        } else {
                            console.log(" ==> ", resp.error_message)
                        }
                    },
                    error(resp) {
                        console.log(" ==> ", resp)
                    }
                })
            },
            change(value, render){
                // render  markdown 
                // console.log(render)
                this.test_html = render;
            },
        },
    }
</script>

<style scoped>
.hljs{
  font-size: 15px;
}
.info{
  border-radius: 10px;
  line-height: 20px;
  padding: 10px;
  margin: 10px;
  background-color: #ffffff;
}
</style>