<template>
    <div class="windows">
        <div class="container" style="margin-top: 20px;">
            <div v-if="this.$route.query.id" style="font-size: 20px; font-weight: 600; text-align: center; color: red;"></div>
            <span style="margin-top:10px;">:</span>
            <el-input
                style="margin-top:10px;"
                placeholder=""
                v-model="title"
                clearable>
            </el-input>
            <span style="margin-top:10px;">:</span>
            <el-input
                style="margin-top:10px;"
                type="textarea"
                :rows="10"
                placeholder=""
                v-model="content">
            </el-input>

            <div style="margin-top:10px;">
                <span>: </span>
                <input type="datetime-local" v-model="selectedDateTime1">
                <p v-if="startTime">Selected Timestamp: {{ startTime }}</p>
            </div>
            
            <div style="margin-top:10px;">
                <span>: </span>
                <input type="datetime-local" v-model="selectedDateTime2">
                <p v-if="closeTime">Selected Timestamp: {{ closeTime }}</p>
            </div>

            <el-input
                style="margin-top:10px;"
                placeholder=""
                v-model="imageName">
            </el-input>

            <span v-if="imageName">  {{ image }}</span>
            <h5 style="margin-top:10px;"></h5>
            <div>
              <input type="file" @change="handleFileUpload" accept="image/*" />
              <img v-if="imageUrl" :src="imageUrl" alt="Uploaded Image" style="max-width: 300px; margin-top: 20px;" />
              <p v-if="imageType">: {{ imageType }}</p>
              <button @click="uploadFile"></button>
            </div>

            <div v-if="error_message" style="margin-top:10px; text-align:center; color:red; font-size:20px">{{ error_message }}</div>
            <el-button v-show="!this.$route.query.id" type="success" plain style="margin-top: 10px; margin-left:603px;" @click="ClickMeToSubmit"></el-button>
            <el-button v-show="this.$route.query.id" type="warning" plain style="margin-top: 10px; margin-left:603px;" @click="ClickMeToUpdate"></el-button>
        </div>
    </div>
</template>

<script>
    import Url from '@/constant/Url'
    import $ from "jquery"

    export default {
    name:'Programmes',
    data() {
        return {
            selectedDateTime1: null,
            selectedDateTime2: null,
            imageName:'',
            
            imageUrl: '',
            imageType: '',
            fileToUpload: null,
            photo:'',
            old_title:'',

            error_message:'',
            title:'',
            startTime:null,
            closeTime:null,
            image:'',
            content:'    ALoffer\n    ACMCF div3    \n    56\n    \n    AC    AC20\n    \n    Compile Error\n\n    \n    50151-20022011001~',
            time:'',
        }
    },
    watch: {
        selectedDateTime1: {
            deep:true,
            immediate:true,
            handler(selectedDateTime) {
                if (selectedDateTime) {
                    // 
                    const selectedDate = new Date(selectedDateTime);
                    this.startTime = selectedDate.getTime();
                }
            }
        },
        selectedDateTime2: {
            deep:true,
            immediate:true,
            handler(selectedDateTime) {
                if (selectedDateTime) {
                    // 
                    const selectedDate = new Date(selectedDateTime);
                    this.closeTime = selectedDate.getTime();
                }
            }
        }
    },
    mounted() {
        
        const query = this.$route.query
        if (query.id)
        {
            this.old_title = query.title,
            this.title = query.title,
            this.selectedDateTime1 = query.startTime
            this.selectedDateTime2 = query.closeTime
            this.imageName = query.image
            this.content = this.content
        }
    },
    methods: {
        ClickMeToUpdate() {
            const vue = this
            const route = this.$route
            const store = this.$store

            $.ajax({
                url: Url.uCompeteUrl,
                type:'post',
                headers: {
                    Authorization: "Bearer " + store.state.me.token,
                },
                data: {
                    id: route.query.id,
                    title: vue.title,
                    startTime: vue.startTime,
                    closeTime: vue.closeTime,
                    image: vue.image,
                    content: vue.content,
                },
                success(resp) {
                    vue.error_message = resp.error_message
                    console.log("old", vue.old_title)
                    console.log("new", vue.title)
                    if (vue.error_message === "success") {
                        $.ajax({
                            url: 'https://app6132.acapp.acwing.com.cn/jdg/competition/passrate/',
                            type: "post",
                            data: {
                                newcontent: vue.title,
                                content: vue.old_title,
                                event: 'reschedule',
                            }
                        })
                    }
                    
                },
                error(resp) {
                    console.log(" resp == >", resp)
                }
            })
        },
        ClickMeToSubmit() { // 
            const vue = this
            const store = this.$store
            
            console.log("image", this.image)
            $.ajax({
                url: Url.cCompeteUrl,
                type:'post',
                headers: {
                    Authorization: "Bearer " + store.state.me.token,
                },
                data: {
                    title: vue.title,
                    startTime: vue.startTime,
                    closeTime: vue.closeTime,
                    image: vue.image,
                    content: vue.content,
                },
                success(resp) {
                    vue.error_message = resp.error_message
                    vue.AddPassRate(vue.title)
                },
                error(resp) {
                    console.log("")
                }
            })
        },
        AddPassRate(title) {
            const vue = this
            const store = this.$store

            $.ajax({
                url: 'https://app6132.acapp.acwing.com.cn/jdg/competition/passrate/',
                type: "post",
                data: {
                    content: title,
                    event: 'create',
                }
            })
        },
        handleFileUpload(event) { // 
          const file = event.target.files[0];
          
          const reader = new FileReader();

          reader.onload = (e) => {
            this.imageUrl = e.target.result; // URL
            // console.log("url == >", this.imageUrl)
            this.fileToUpload = file; // 
            this.photo = file.name
            // 
            const extension = file.name.split('.').pop().toLowerCase();
            // MIME
            const fileType = file.type;
            // console.log(file)
            console.log("extension", extension)
            this.image = "https://app6132.acapp.acwing.com.cn/static/image/" +  this.imageName + "." + extension
            // console.log("type", fileType)
            // 
            this.imageType = fileType;
          };

          if (file === null || file === undefined) {
            this.photo = ""
            this.imageUrl = ""
            this.imageType = ""
            
            console.log("")
            return
          }
          // DataURL
          reader.readAsDataURL(file);
        },
        uploadFile() { //  url
          if (!this.fileToUpload || this.imageName === "" || this.imageName === null) {
            console.error('  ');
            return;
          }

          // FormData
          const formData = new FormData();
          formData.append('image', this.fileToUpload);
          formData.append('userid', this.imageName)
          // $.ajaxURL/upload
          $.ajax({
            url: 'https://app6132.acapp.acwing.com.cn/jdg/upload/classes/',
            method: 'POST',
            data: formData,
            processData: false,
            contentType: false,
            success: (response) => {
              console.log('');
              // 
            },
            error: (error) => {
              console.error(':', error);
            }
          });
        },
    }
  }
</script>
 
<style scoped>
.windows {
  width: 100%; 
  min-height: 800px;
  height: auto;
}
</style>