import $ from 'jquery'

export default {
    namespaced:true,

    //actions--actions
    actions: {
      GetQuestionList(context) { // 
        $.ajax({
          url: "https://app6132.acapp.acwing.com.cn/api/question/get/",
          type: "post",
          // headers: {
          //   Authorization: "Bearer " + context.rootState.me.token // 
          // },
          success(resp) {
            // console.log(" resp == >", resp)
            context.commit("UPDATEQUESTION", resp)
          },
          error(resp) {
            console.log(" resp == > ", resp)
          }
        })
      },
      SelectQuestionList(context, obj) { //  
        const new_list = JSON.stringify(obj.list)
        // console.log("option == >", obj.option)
        $.ajax({
          url:"https://app6132.acapp.acwing.com.cn/api/select/question/",
          type: "get",
          data: {
            list: new_list,
            option: obj.option,
            input: obj.input,
            pbfrom: obj.pbfrom,
          },
          success(resp) {
            if (resp.error_message === 'success') {
              // console.log(", resp == >", resp);
              const List = JSON.parse(resp.list);
              // console.log(" List == >", List)
              context.commit("UPDATEQUESTION", List)
            }
            // } else {
            //   console.log(", resp == >", resp.error_message)
            //   const List = JSON.parse(resp.list);
            //   context.commit("UPDATEQUESTION", List)
            // }
          },
          error(resp) {
            console.log(", resp == >", resp)
          }
        })
      },
      SelectExampleList(context, pbid) {
        $.ajax({
          url: "https://app6132.acapp.acwing.com.cn/api/example/select/",
          type: "post",
          data: {
            pbid: pbid,
          },
          headers: {
            Authorization: "Bearer " + context.rootState.me.token // 
          },
          success(resp) {
            const List = JSON.parse(resp.List)
            // console.log(" resp == >", List)
            context.commit("UPDATEEXAMPLE", List)
          },
          error(resp) {
            console.log(" resp == > ", resp)
          }
        })
      },
    },

    //mutations--
    mutations: {
      UPDATERECORD(state, data) { /*   */
        state.submitRecord.push(data)
      },
      UPDATEQUESTION(state, data) { /*    30 / 10  */
        state.question = data
        // console.log(": ", state.question)
      },
      UPDATEEXAMPLE(state, data) {
        const list = data;

        state.example = list
        // console.log("", state.example)
      },
      UPDATENOTES(state, data) {
        state.notes.id = data.id;
        state.notes.pbid = data.pbid
        state.notes.title = data.title
        state.notes.markdown = data.markdown
        state.notes.content = data.content
        state.notes.createtime = data.createtime
        state.notes.userid = data.userid
        state.notes.name = data.name
        state.notes.photo = data.photo
        state.notes.goods = data.goods

        console.log("notes == >", state.notes)
      }
    },

    //getters state
    getters: {
        
    },

    //state--
    state: {
      /*   */
      submitRecord: [{
          submittime: '2016-05-02',
          status: 'Accept',
          lung: 'C++',
          runtime: '114514ms',
          code: "cout << a + b << endl; ",
        }, {
          submittime: '2016-05-02',
          status: 'Accept',
          lung: 'C++',
          runtime: '114514ms',
          code: "cout << a - b << endl; ",
        }, {
          submittime: '2016-05-02',
          status: 'Accept',
          lung: 'C++',
          runtime: '114514ms',
          code: "cout << a * b << endl; ",
        }, {
          submittime: '2016-05-02',
          status: 'Accept',
          lung: 'C++',
          runtime: '114514ms',
          code: "cout << a / b << endl; ",
        }, 
      ],
      
      /*  */
      question:[],

      /*  */
      example:[],

      /*  */
      notes: {
        id:'',
        pbid:'',
        title:'',
        markdown:'',
        content:'',
        createtime:'',
        userid:'',
        name:'',
        photo:'',
        goods:'',
      }
    }
}