<template>
    <div class="header">
        <div style="float: right; margin-right:50px; margin-top: 20px; margin-bottom: 5px;">
            <el-button type="success" round @click="ClickMeToAdd"></el-button>
        </div>
        <el-table
        :data="courseList"
        border
        style="width: 1170px; margin: 0 auto;"
        min-height="200"
        max-height="500">
        <el-table-column
            prop="createTime"
            label=""
            width="234">
        </el-table-column>
        <el-table-column
            prop="courseName"
            label=""
            width="234">
        </el-table-column>
        <el-table-column
            prop="price"
            label=""
            width="234">
        </el-table-column>
        <el-table-column
            prop="participants"
            label=""
            width="234">
        </el-table-column>
        <el-table-column
            fixed="right"
            label=""
            width="233">
            <template slot-scope="scope">
            <el-button @click="handleClick(scope.row)" type="text" size="small"></el-button>
            <el-button @click="ClickMeToUpdate(scope.row)" type="text" size="small"></el-button>
            </template>
        </el-table-column>
        </el-table>
  </div>
</template>

<script>
    import $ from 'jquery'
    export default {
        methods: {
            handleClick(row) {
                let routeData = this.$router.resolve({
                    name:'programmes',
                    params: { //  
                        id: row.id,
                    }
                });
                window.open(routeData.href, '_blank');
            },
            ClickMeToUpdate(row) {
                console.log(row)
                let routeData = this.$router.resolve({
                    path:'/AboutCourse',
                    query: { //  
                        id: row.id,
                        words: row.words,
                        course_name: row.courseName,
                        photo: row.photo,
                        price: row.price,
                        course_about: row.courseAbout
                    }
                });
                window.open(routeData.href, '_blank');
            },
            ClickMeToAdd() {
                let routeData = this.$router.resolve({
                    path:'/AboutCourse',
                });
                window.open(routeData.href, '_blank');
            }
        },
        data() {
            return {
                jsonList:'',
                courseList:[],
            }
        },
        mounted() {
            const vue = this
            $.ajax({
                url:'https://app6132.acapp.acwing.com.cn/api/course/read/list/',
                type:'get',
                success(resp) {
                    if (resp.error_message === 'success') {
                        // console.log("")
                        vue.jsonList = resp.courseList
                        vue.courseList = JSON.parse(vue.jsonList)
                        // console.log(vue.courseList)
                    }
                },
                error(resp) {
                    console.log(", resp == >", resp)
                }
            })
        }
    }
</script>

<style scoped>
.header {
    border: 1px solid lightgrey;
}
</style>