import {nanoid} from 'nanoid'

export default {
    namespaced:true,

    //actions--actions
    actions: {
        
    },

    //mutations--
    mutations: {
        ADDPERSONS(minstate, value) {
            minstate.MyPersons.length = 0 /*  */
            for (let i = 0; i < value.length; i ++ ) {
                minstate.MyPersons.push({
                    id:value[i].id,
                    name:value[i].name,
                    pho:value[i].pho,
                    msg:'',
                    msgs:[],
                })
            }
            console.log(minstate.MyPersons)
            // minstate.id = value.id
            // minstate.name = value.name
            // minstate.pho = value.pho
        },
        UPDATERECEIVEID(minstate, value) {
            minstate.receiveId = value  /* id */
        },
        UPFRIEND(state, value) {
            state.firend = value;
        },
    },

    //getters state
    getters: {
        
    },

    //state--
    state: {
        name:'', /*  */
        zh:'', /*  */
        password:'', /*  */
    
        receiveId:'',/* id */
        MyPersons:[],
        firend:[],
    },
}