export default {
    namespaced:true,

    //actions--actions
    actions: {
      
    },

    //mutations--
    mutations: {
        UPDATECOURSE(state, data) {
            state.course.id = data.id
            state.course.courseName = data.courseName
            state.course.photo = data.photo
            state.course.participants = data.participants
            state.course.price = data.price
            state.course.courseAbout = data.courseAbout
            state.course.words = data.words
            state.course.createTime = data.createTime
        }
    },

    //getters state
    getters: {
        
    },

    //state--
    state: {
        /*  */
        course:{
            id:'',
            courseName:'',
            photo:'',
            participants:'',
            price:'',
            courseAbout:'',
            words:'',
            createTime:'',
        },
    }
}