<template>
    <div class="windows">
      <div class="container" style="margin-top: 20px;">
        <div class="row">
            <div class="col-12">
                <div style="margin-top:40px; position: fixed; top: 30%; right: 20px; transform: translateY(-50%); z-index: 10;">
                    <el-button style="margin-bottom: 10px;" v-show="this.$route.query.id" type="danger" icon="el-icon-delete" circle @click="ClickMeToDel"></el-button>
                    <div style="margin-bottom: 10px;"><el-button type="primary" icon="el-icon-edit" circle @click="close"></el-button></div>
                    <div style="margin-bottom: 10px;"><el-button type="success" icon="el-icon-check" circle @click="open"></el-button></div>
                    <div style="margin-bottom: 10px;"><el-button type="warning" round @click="ClickMeToSubmit"></el-button></div>
                </div>
            </div>
        </div>
        <div v-show="this.$route.query.id" class="row">
          <div class="col-12 text-center" style="font-size: 20px; color: red; font-weight: 600;">
            
          </div>
        </div>
        <el-form label-position="left" label-width="80px">
            <el-form-item label="">
                <el-input v-model="course_name"></el-input>
            </el-form-item>
            <el-form-item label="">
                <el-input v-model="price"></el-input>
            </el-form-item>
        </el-form>
        <h4></h4>

        <!-- <div>
          <input type="file" @change="handleFileUpload" accept="image/*" />
          <img v-if="imageUrl" :src="imageUrl" alt="Uploaded Image" style="max-width: 300px; margin-top: 20px;" />
          <p v-if="imageType">: {{ imageType }}</p>
          <button @click="uploadFile"></button>
        </div> -->

        <el-input
            style="margin-top:10px;"
            placeholder=""
            v-model="imageName">
        </el-input>

        <span v-if="imageName" style="margin-top: 10px;">  {{ image }}</span>
        <div style="margin-top: 10px;">
          <input type="file" @change="handleFileUpload" accept="image/*" />
          <img v-if="imageUrl" :src="imageUrl" alt="Uploaded Image" style="max-width: 300px; margin-top: 20px;" />
          <p v-if="imageType">: {{ imageType }}</p>
          <button @click="uploadFile"></button>
        </div>

        <!--    -->
        <div class="row">
          <div class="col-8" >
            <img src="@/assets/kcimg/studyed-title.png" alt="" width="120px">
            <div style="background-color:#F5F9F9; min-height: 100px; height: auto; border-radius: 10px; margin-top: 15px;" >
              <div class="container" style="padding: 20px; font-size: 14px; font-weight: 600; min-height: 100px; height: auto;">
                <div v-show="isShow" class="row g">

                  <div class="col-6" v-for="(l,index) in list" :key="index">
                    <div class="p-3"><li>{{ l }}</li></div>
                  </div>
                </div>
                <el-input
                    v-show="!isShow"
                    type="textarea"
                    :rows="2"
                    placeholder=""
                    v-model="words">
                </el-input>
              </div>
            </div>
          </div>

          <div class="col-4" style="font-size:16px;">
            <div class="col text-center" style=""> 
              <div style="background-color:#162E3E; width: 70%; margin: 20px auto 0px; height: 150px; box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1)">
                <img src="@/assets/kcimg/ds.png" alt="" width="40%">
                <div style="text-align: center; color: white; font-weight: 600;"></div>
              </div> 
              <div style="background-color:white; width: 70%; margin: 0px auto; height: 40px;  box-shadow: 0 2px 4px rgba(0, 0, 0, .12), 0 0 6px rgba(0, 0, 0, .04)">
                <span style="line-height: 40px;">1-1</span>
              </div> 
              <el-button type="warning" style="margin-top: 10px;"></el-button>
            </div>
          </div>
        </div>
        <!--  -->
        <div class="row" style="padding-top: 10px;">
          <div class="col">
            <img src="@/assets/kcimg/course-desc-title.png" alt="" width="120px">
            <div v-show="isShow" style="font-size: 15px; padding: 25px;">
              {{ course_about }}
            </div>
            <el-input
                v-show="!isShow"
                type="textarea"
                :rows="2"
                placeholder=""
                v-model="course_about">
            </el-input>
          </div>
        </div>

        <CourseCatalog></CourseCatalog>
      </div>
    </div>
</template>

<script>
  //boostrap   js
  import CourseCatalog from './CourseCatalog.vue'
  import $ from 'jquery'
  export default {
    name:'Programmes',
    components: {
      CourseCatalog
    },
    data() {
        return {
          imageName:'',
          image:'',

          imageUrl: '',
          imageType: '',
          fileToUpload: null,

          isShow:true,
          list:[],
          words:'300+1+&C++&01C++&&C++11&100+',
          course_name:'',
          photo:'',
          participants:'',
          price:999,
          course_about:'AL""  01C++'
        }
    },
    watch: {
      words: {
        deep:true,
        immediate:true,
        handler(value) {
          this.list = this.words.split('&')
        }
      }
    },
    created() {
      if (this.$route.query.id !== undefined) {
        const query = this.$route.query
        // console.log("query == > ", this.$route.query)
        this.course_about = query.course_about
        this.course_name = query.course_name
        this.words = query.words
        this.price = query.price
        this.photo = query.photo
      }
    },
    methods: {
        handleFileUpload(event) { // 
          const file = event.target.files[0];
          
          const reader = new FileReader();

          reader.onload = (e) => {
            this.imageUrl = e.target.result; // URL
            // console.log("url == >", this.imageUrl)
            this.fileToUpload = file; // 
            this.photo = file.name
            // 
            const extension = file.name.split('.').pop().toLowerCase();
            // MIME
            const fileType = file.type;
            // console.log(file)
            console.log("extension", extension)
            this.image = "https://app6132.acapp.acwing.com.cn/static/image/" +  this.imageName + "." + extension
            // console.log("type", fileType)
            // 
            this.imageType = fileType;
          };

          if (file === null || file === undefined) {
            this.photo = ""
            this.imageUrl = ""
            this.imageType = ""
            
            console.log("")
            return
          }
          // DataURL
          reader.readAsDataURL(file);
        },
        uploadFile() { //  url
          if (!this.fileToUpload || this.imageName === "" || this.imageName === null) {
            console.error('  ');
            return;
          }

          // FormData
          const formData = new FormData();
          formData.append('image', this.fileToUpload);
          formData.append('userid', this.imageName)
          // $.ajaxURL/upload
          $.ajax({
            url: 'https://app6132.acapp.acwing.com.cn/jdg/upload/classes/',
            method: 'POST',
            data: formData,
            processData: false,
            contentType: false,
            success: (response) => {
              console.log('');
              // 
            },
            error: (error) => {
              console.error(':', error);
            }
          });
        },
        close() {
          this.isShow = false
        },
        open() {
          this.isShow = true
        },
        ClickMeToSubmit() {
          const vue = this;
          const store = this.$store;
          const route = this.$route

          if (this.$route.query.id === undefined) { // 
            $.ajax({
              url:'https://app6132.acapp.acwing.com.cn/api/course/create/',
              type:'post',
              headers: {
                Authorization: "Bearer " + store.state.me.token,
              },
              data: {
                course_name: vue.course_name,
                course_about: vue.course_about,
                photo: vue.image,
                price: vue.price,
                words: vue.words,
              },
              success(resp) {
                if (resp.error_message === "success") {
                  console.log("")
                } else {
                  console.log(", resp == >", resp.error_message)
                }
              },
              error(resp) {
                console.log(", resp == >", resp)
              }
            })
          } else {
            $.ajax({
              url:'https://app6132.acapp.acwing.com.cn/api/course/update/',
              type:'post',
              headers: {
                Authorization: "Bearer " + store.state.me.token,
              },
              data: {
                id: route.query.id,
                course_name: vue.course_name,
                course_about: vue.course_about,
                photo: vue.image,
                price: vue.price,
                words: vue.words,
              },
              success(resp) {
                if (resp.error_message === "success") {
                  console.log("")
                } else {
                  console.log(", resp == >", resp.error_message)
                }
              },
              error(resp) {
                console.log(", resp == >", resp)
              }
            })
          }
        },
        ClickMeToDel() {
          const route = this.$route
          const store = this.$store
          const message = this.$message
          this.$confirm(', ?', '', {
              confirmButtonText: '',
              cancelButtonText: '',
              type: 'warning'
          }).then(() => {
            
            $.ajax({
              url:"https://app6132.acapp.acwing.com.cn/api/course/delete/",
              type:'post',
              headers: {
                Authorization: "Bearer " + store.state.me.token,
              },
              data: {
                id: route.query.id
              },
              success(resp) {
                if (resp.error_message === "success") {
                  console.log(", resp == > ", resp.error_message)
                  message({
                      type: 'success',
                      message: '! 3'
                  });
                  setTimeout(() => {
                      window.close()
                  }, 3000);
                } else {
                  message({
                      type: 'warning',
                      message: '.... resp == >' + resp.error_message
                  });
                }
              },
              error(resp) {
                message({
                    type: 'warning',
                    message: '....' + resp
                });
              }
            })
          }).catch(() => {
            message({
                type: 'info',
                message: ''
            });          
          });
        },
    }
  }
</script>

